import { Component, Input, OnInit } from '@angular/core';
import { PaymentTypeConfig } from '../../../configs/models/form-items.model';
import { getLabelFromPaymentConfig } from '../../helpers/payment-config-helpers';

@Component({
  selector: 'bb-payord-dynamic-label',
  template: ` <ng-container>{{ label || defaultLabel }}</ng-container> `,
})
export class PayordDynamicLabelComponent implements OnInit {
  /**
   * The payment config
   */
  @Input() config!: PaymentTypeConfig;
  /**
   * Name of the form field
   */
  @Input() name!: string;
  /**
   * Fallback label name
   */
  @Input() defaultLabel = '';
  /**
   * Parent group name of the form field
   */
  @Input() parentName = '';

  label = '';

  ngOnInit() {
    this.label = getLabelFromPaymentConfig(
      this.name,
      this.config,
      this.parentName
    );
  }
}
